@import "./colors";

circle {
  opacity: 0.5;
  fill: $base-blue;
  stroke: black;
}

circle.all-star {
  fill: $base-red;
}

circle.legend {
  fill: white;
}

.tick line {
  stroke: lightgray;
}

.axis path {
  stroke: lightgray;
}

.axis-label {
  font-weight: 700;
  font-size: 16px;
}

.tooltip {
  background: whitesmoke;
  opacity: 90%;
  padding: 10px;
  border-radius: 10px;
  border: 1px solid black;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: max-content;
  max-width: 240px;
}

strong, .strong {
  margin-bottom: 5px;
  font-weight: 600;
}

.tooltip-logo-line {
  display: flex;
  align-items: center;
  margin-bottom: 5px;
  img {
    margin-left: 3px;
  }
  strong {
    white-space: nowrap;
  }
}

.graph-title {
  font-weight: 700;
  font-size: 20px;
}

.vis-scatter {
  margin-top: 60px;
  display: flex;
  width: 100%;
  justify-content: center;
  height: 800px;
}

.legend-selects {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  :first-child {
    align-self: flex-end;
  }

  .scatter-selects {
    display: table;

    div {
      display: table-row;
    }

    label, select {
      display: table-cell;
      margin: 3px 0px;
    }

    label {
      text-align: right;
      padding-right: 10px;
    }
  }
}
